.content-rxn
{
    text-align: left;
    padding: 2vh 2vw 2vh 2vw;
   background-color: white;
   box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}
.rx-desc{
    
    font-size: 18px;
}
.rx-head{
    text-align: left;
    font-size: 30px;
    font-family: Inika;
    color: blueviolet;
}
.head-det{
    display: flex;
    justify-content: space-between;
}
.mode{
   
    width: .8cm;
    height: .8cm;
    cursor: pointer;
    user-select: none;
  }

@media screen and (max-width:500px){
    .mode{
        width: .7cm;
        height: .7cm;
    }
  }

  
@media print{
    .heading-dr{
        display: none;
    }
    .dr{
        display: none;
    }
}
@page{
    size: A4;
}
@media screen and (max-width:600px){
    .rx-desc{
        font-size: 17px;
    }
    
    h2{
        font-size: 22px;
    }
    h3{
        font-size: 19px;
    }
   

} 
@media screen and (max-width:500px){
    .rx-desc{
        font-size: 16px;
    }
   
        h2{
            font-size: 20px;
        }
        h3{
            font-size: 17px;
        }
   
    .rx-head{
        
        font-size: 25px;
       
    }

}
@media screen and (max-width:400px){
    .rx-desc{
        font-size: 16px;
    }
    
        h2{
            font-size: 19px;
        }
        h3{
            font-size: 16px;
        }
   
    .rx-head{
        
        font-size: 23px;
       
    }
}
@media screen and (max-width:300px){
    .rx-desc{
        font-size: 15px;
    }
    
        h2{
            font-size: 18px;
        }
        h3{
            font-size: 15px;
        }
   
    .rx-head{
        
        font-size: 21px;
       
    }
}
@media screen and (max-width:280px){
    .rx-desc{
        font-size: 13px;
    }
   
        h2{
            font-size: 16px;
        }
        h3{
            font-size: 13px;
        }
   
    .rx-head{
        
        font-size: 18px;
       
    }
}
